@import url(https://fonts.googleapis.com/css?family=Lato|Montserrat:500,600|Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #1c1c1c;
  color: #FFFFFF;
  font-family: Raleway;
}

.custom-nav {
  background-color: #0c0c0c;
  width: 100%;
  display: flex;
  padding: 10px;
  justify-content: space-between;
}

.icon-group {
  display: flex;
  align-content: center;
}

.icon-group img{
  margin: 10px 30px;
  height: 40px;
}

.hover-group {
  position: absolute;
}

.icon-base {
  transition: opacity .3s;
}

.icon-base:hover {
  opacity: 0;
}

.hamburger {
  display: flex;
  align-content: center;
}

.hamburger img{
  margin: 10px 10px;
  width: 40px;
}

.gradient {
  height: 2px;
  background-image: linear-gradient(to right, #FFBF00,#5ADBFF,#5ADBFF,#EF2D56);
}

.hero-image {
  background-image: url(/portfolio/static/media/splash.aefad542.png);
  background-color: #cccccc;
  height: 80vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-text {
  text-align: center;
  position: absolute;
  width: 40%;
  top: 50%;
  left: 25%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.showcase-row {
  padding: 15px;
}

.showcase-title {
  font-family: Montserrat;
  font-size: 44px;
  font-weight: 500;
  width: 100%;
}

.showcase-body {
  font-size: 16px;
}

.showcase-link div{
  position: absolute;
  bottom: 50px;
}

.skill-card {
  background-color: #0c0c0c;
  border: solid #4e4e4e 1px;
  margin-bottom: 30px;
}

.skill-img-container {
  height: 200px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position:center center;
  margin-top: 15px;
}

.skill-img-container img{
  object-fit: contain;
}

.skill-card-text {
  text-align: center;
  font-size: 40px;
  font-weight: 600;
}

.contact-card {
  color: #EF2D56;
  transition: opacity .3s;
  transition: color .3s;
}

.contact-card a {
  text-align: center;
}

.contact-card a:hover {
  text-decoration: none;
}

.contact-hover-group {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.contact-icon-base {
  color: #FFFFFF;
  transition: opacity .3s;
}

.contact-hover {
  color: #EF2D56;
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
}

.contact-icon-base:hover {
  opacity: 0;
}

.contact-img-container {
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position:center center;
  margin-top: 15px;
}

.contact-img-container img{
  object-fit: contain;
}

.contact-card-text {
  font-size: 40px;
  font-weight: 600;
}

.title {
  text-align: center;
  font-family: Montserrat;
  font-weight: 600;
  font-size: 60px;
  padding: 20px;
}

.subtitle {
  text-align: center;
  font-family: Montserrat;
  font-weight: 500;
  font-size: 50px;
  padding: 20px;
}

.profile-pic {
  width: 250px;
  border-radius: 250px;
}

.contact-info {
  text-align: center;
  font-weight: 700;
  font-size: 40px;
  padding: 20px;
}

